<template>
  <div>
    <form class="formLesson form-inline container" @submit.prevent="toUpload">
      <p class="numberTitle mb-5 mt-5">Lesson Number</p>
      <input type="text" class="form-control mb-3" id="lessonNumber" v-model="lessonNumber" @keyup="keyInput">
      <button type="submit" class="btn btn-primary btn-block submitBtn" :disabled="disabled" >Submit</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'UPLOAD-PANEL',
  data () {
    return {
      msg: 'Welcome to ChinesePod Upload Panel',
      lessonNumber: "",
      disabled: true
    }
  },
  methods:{
    toUpload(){
      this.$router.push("/upload-page/" + this.lessonNumber);
    },
    keyInput() {
      if(this.lessonNumber == ""){
        this.disabled = true; 
      }else {
        this.disabled = false;        
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.formLesson {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.numberTitle {
  color: #2487C1;
  font-weight: normal;
  margin: auto;
  font-size: 22px;
}
.form-control {
  width: inherit;
  border-radius: 5px;
}
.btn-primary {
  border-radius: 5px;
  background-color: #2487C1;
  border-color: #2487C1;
  letter-spacing: 2.1px;
}
.btn-primary:not(:disabled):hover {
  background-color: #2177a9;
}
</style>



