<template>
  <uploader :options="options" :file-status-text="statusText" class="uploader container" ref="uploader" @file-complete="fileComplete" @complete="complete"></uploader>
</template>

<script>
 let vm = ""
  export default {
    data () {
      return {
        options: {
          target: '/api/v1/recap/upload', // '//jsonplaceholder.typicode.com/posts/',
          testChunks: false,
          headers: {
            'Access-Control-Allow-Origin': "*",
            "Accept": "*/*"

          },
          processParams: function(){
            let lesson = vm.$route.params.id
            return {'lessonId': lesson}
          }
        },
        attrs: {
          accept: 'image/*'
        },
        statusText: {
          success: 'Success',
          error: 'Error',
          uploading: 'Uploading',
          paused: 'Pause',
          waiting: 'Waiting'
        },
        lessonNumber : ""
      }
    },
    methods: {
      complete () {
        console.log('complete', arguments)
      },
      fileComplete () {
        console.log('file complete', arguments)
      }
    },
    mounted () {
      this.$nextTick(() => {
        window.uploader = this.$refs.uploader.uploader
      })
    },
    created () {
      vm = this
      this.lessonNumber = this.$route.params.id
    }
  }
</script>

<style>
  .uploader {
    padding: 15px;
    margin: auto;
    font-size: 12px;
  }
  .uploader .uploader-btn {
    z-index: 1000;
  }
  .uploader .uploader-list {
    max-height: 440px;
    overflow: auto;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>



