import Vue from 'vue'
import Router from 'vue-router'
import UploadToLesson from '@/components/UploadToLesson'
import UploadPage from '@/components/UploadPage'

Vue.use(Router)

export default new Router({
  // base: '/recap/upload-files',
  routes: [
    {
      path: '*',
      redirect: '/',
    },
    {
      path: '/',
      name: 'UploadToLesson',
      component: UploadToLesson
    },
    {
      path: '/upload-page/:id',
      name: 'UploadPage',
      component: UploadPage,
    }
  ]
})





