<template>
  <div class="uploader-drop" :class="dropClass" ref="drop" v-show="support">
    <slot></slot>
  </div>
</template>

<script>
  import { uploaderMixin, supportMixin } from '../common/mixins'

  const COMPONENT_NAME = 'uploader-drop'

  export default {
    name: COMPONENT_NAME,
    mixins: [uploaderMixin, supportMixin],
    data () {
      return {
        dropClass: ''
      }
    },
    methods: {
      onDragEnter () {
        this.dropClass = 'uploader-dragover'
      },
      onDragLeave () {
        this.dropClass = ''
      },
      onDrop () {
        this.dropClass = 'uploader-droped'
      }
    },
    mounted () {
      this.$nextTick(() => {
        const dropEle = this.$refs.drop
        const uploader = this.uploader.uploader
        uploader.assignDrop(dropEle)
        uploader.on('dragenter', this.onDragEnter)
        uploader.on('dragleave', this.onDragLeave)
        uploader.on('drop', this.onDrop)
      })
    },
    beforeDestroy () {
      const dropEle = this.$refs.drop
      const uploader = this.uploader.uploader
      uploader.off('dragenter', this.onDragEnter)
      uploader.off('dragleave', this.onDragLeave)
      uploader.off('drop', this.onDrop)
      uploader.unAssignDrop(dropEle)
    }
  }
</script>

<style>
  .uploader-drop {
    position: relative;
    padding: 10px;
    overflow: hidden;
    height: 350px;
    /* background-color: #f5f5f5; */
    border-radius: 5px;
    /* background: linear-gradient(to right, #7AB3D3 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(#7AB3D3 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(to right, #7AB3D3 50%, rgba(255, 255, 255, 0) 0%), linear-gradient(#7AB3D3 50%, rgba(255, 255, 255, 0) 0%); */
    background-position: top, right, bottom, left;
    background-repeat: repeat-x, repeat-y;
    background-size: 20px 1px, 1px 20px;
    border: 10px solid #eff6fa;
  }
  .uploader-dragover {
    border-color: #eff6fa;
    background-color: #f7f7f7;
  }
  .uploader-dragover > * {
    transition: opacity 0.3s ease;
    opacity: 0.3;
  }  
    .uploader-dragover .overlay {
    opacity: 1;
  }
</style>



